import React from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import CustomInput from "../../components/CustomInput";
import ModalNotification from "../../components/ModalNotification";
import TopNav from "../../components/TopNav";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import { login } from "../../redux/actions/auth";
import failedImg from "../../assets/hand_fail.png";
import succImg from "../../assets/hand_succ.png";
const useStyles = makeStyles(() => ({
  root: {
    paddingTop: "16px",
  },
}));

const Login = (props) => {
  const { actLogin, stateBahasa, stateUser } = props;
  const classes = useStyles();
  const [type, setType] = React.useState("");
  const [typeVal, setTypeVal] = React.useState("MR");
  const [openSucc, setOpenSucc] = React.useState(false);
  const [openFail, setOpenFail] = React.useState({
    open: false,
    message: "",
  });
  const navigate = useNavigate();

  React.useEffect(() => {
    if (stateUser) {
      navigate("/home", {
        replace: true,
      });
    }
  }, [stateUser, navigate]);

  const handleFail = (msg) => {
    setOpenFail({
      open: true,
      message: msg,
    });
  };
  const [password, setPassword] = React.useState("");
  const [noMr, setNoMr] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [email, setEmail] = React.useState("");

  const changePwd = (e) => {
    setPassword(e.target.value);
  };
  const handleType = (e) => {
    setType("");
    setTypeVal(e.target.value);
  };

  const handleLogin = () => {
    return actLogin(
      {
        no_tlp: phone,
        email: email,
        no_mr: noMr ? noMr.replace(/-/g, "") : "",
        password: password,
      },
      {
        success: () => setOpenSucc(true),
        failed: (msg) => handleFail(msg),
      }
    );
  };
  return (
    <>
      <ModalNotification
        open={openSucc}
        setOpen={() => navigate("/home")}
        title={"Logged In"}
        subtitle={""}
        img={succImg}
        actAbort={() => navigate("/home")}
      />
      <ModalNotification
        open={openFail.open}
        setOpen={(open) => setOpenFail({ ...openFail, open: open })}
        title={"Failed"}
        subtitle={openFail.message}
        img={failedImg}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav type="back" title="Login" click={() => navigate(-1)} />
          <FormControl fullWidth>
            <InputLabel id="type">Type</InputLabel>
            <Select
              labelId="type"
              id="demo-simple-select"
              value={typeVal}
              label="Type"
              onChange={handleType}
            >
              <MenuItem value={"MR"}>No MR</MenuItem>
              <MenuItem value={"Tlp"}>Telp</MenuItem>
              <MenuItem value={"Email"}>Email</MenuItem>
            </Select>
          </FormControl>
          {typeVal === "MR" && (
            <CustomInput
              inputType="no_mr"
              value={noMr}
              onChange={(e) => setNoMr(e.target.value)}
              id="no_mr"
              name="no_mr"
              label="No MR"
            />
          )}
          {typeVal === "Email" && (
            <CustomInput
              inputType="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              id="email"
              name="email"
              label="Email"
            />
          )}
          {typeVal === "Tlp" && (
            <CustomInput
              inputType="phone-number"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              id="phone"
              name="phone"
              label="No Telp"
            />
          )}

          <TextField
            id="outlined-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
            value={password}
            onChange={changePwd}
          />
          <Button
            variant="contained"
            onClick={handleLogin}
            disabled={!password}
            // onClick={() => navigate("/")}
          >
            Login
          </Button>
          <div>
            <Button variant="text" onClick={() => navigate("/register-user")}>
              {stateBahasa === "indonesia"
                ? "Tidak memiliki akun? Daftar"
                : "Don't have account? Register"}
            </Button>
            <Typography variant="body1" align="center">
              Or
            </Typography>
            <Button variant="text" onClick={() => navigate("/home")}>
              {stateBahasa === "indonesia"
                ? "Masuk sebagai tamu"
                : "Sign-in as guest"}
            </Button>
          </div>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateBahasa: state.app.bahasa,
    stateUser: state.auth.token,
    // stateCompany: state.app.company,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actLogin: (body, action) => dispatch(login(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
